<template>
  <div class="course-box">
    <div class="c-top">
      <ul class="c-list">
        <li v-for="tab in tabs" :key="tab.type" @click="changeType(tab.type)">
          <span :class="{'check': tab.type == type}">{{tab.name}}</span>
        </li>
      </ul>
    </div>
    <div class="c-content">
      <ul class="c-list">
        <li v-for="(item, index) in publicCourseList" :key="index">
          <PublicClassList :item="item" :params="params"></PublicClassList>
        </li>
      </ul>
      <!-- 暂无数据 -->
      <div class="no-data" v-if="publicCourseList.length === 0 && noData">
        <img src="../../../static/images/no-data.png" />
        <div>
          <span>暂无公开课 敬请期待</span>
        </div>
      </div>
    </div>
    <div class="c-bottom" v-if="total>0">
      <el-pagination
        background
        layout="prev, pager, next"
        @current-change="changePage"
        :current-page="parseInt(params.page_num, 10)"
        :total="total"
      ></el-pagination>
    </div>
  </div>
</template>
<script>
import { getPublicClass, getPublicUrl } from "api/study";
import PublicClassList from "../../components/studyCenter/PublicClassList";
export default {
  components: { PublicClassList },
  data() {
    return {
      noData: false,
      tabs: [
        {
          name: "全部",
          type: ""
        },
        {
          name: "即将开始",
          type: "0"
        },
        {
          name: "精彩回放",
          type: "1"
        }
      ],
      total: 0,
      publicCourseList: [],
      params: {
        type: this.$route.query.type || "",
        page_num: this.$route.query.page_num || "1",
        page_size: "10"
      },
      type: this.$route.query.type || "" // 默认公开课
    };
  },
  mounted() {
    // 请求课程
    this.getPublicClass(this.params);
  },
  methods: {
    // 获取数据
    getPublicClass(data) {
      getPublicClass({
        data: data,
        success: res => {
          let data = res.data;
          this.publicCourseList = data.course_list;
          this.total = parseInt(data.page.total, 10);
          window.scrollTo(0, 0);
        },
        error: err => {
          console.log("失败", err);
        }
      });
    },
    // 切换页码
    changePage(val) {
      this.params.page_num = `${val}`;
      this.getPublicClass(this.params);
    },
    // 切换类型
    changeType(type) {
      this.publicCourseList = [];
      this.total = 0;
      this.type = type;
      this.params.page_num = "1";
      this.params.type = type;
      this.getPublicClass(this.params);
    }
  },
  watch: {
    publicCourseList(newVal) {
      this.noData = newVal.length === 0;
    }
  }
};
</script>

<style lang="less" scoped>
.course-box {
  background: #ffffff;
  margin-left: 335px;
  width: calc(~"100% - 330px");
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  .c-top {
    width: 100%;
    position: fixed;
    top: 0;
    background: #ffffff;
    height: 92px;
    line-height: 92px;
    border-bottom: 2px solid #efefef;
    .c-list {
      display: flex;
      margin-left: 47px;
      li {
        cursor: pointer;
        margin-right: 40px;
        font-size: 16px;
        color: #666666;
        span {
          &.check {
            font-size: 20px;
            color: #333333;
          }
        }
      }
    }
  }
  .c-content {
    .c-list {
      margin: 127px 52px 0 48px;
      li {
        // cursor: pointer;
        min-width: 700px;
        min-height: 53px;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 0px 10px 0px rgba(236, 236, 238, 0.7);
        border-radius: 3px;
        margin-bottom: 30px;
        padding: 36px;
        &:hover {
          box-shadow: 0 1px 23px 1px rgba(45, 117, 212, 0.13);
        }
      }
    }
    .no-data {
      height: calc(~"100vh - 140px");
      img {
        margin-top: calc(~"50vh - 140px");
        width: 208px;
      }
    }
  }
  .c-bottom {
    margin-bottom: 50px;
  }
}
// 媒体查询
@media (max-width: 1440px) {
  .main-page .course-box {
    width: calc(~"100% - 249px");
    margin-left: 249px;
  }
}
</style>




